@import '../app.scss';

#projects {
    padding-top: 4rem;
    padding-bottom: 3rem;
    background-color: $bg-dark-variant;
    text-align: center;

    #projects-section-title {
        margin-bottom: 2rem;
    }

    /* Projects Content */
    #projects-wrapper {
        margin: 0;
        padding: 0;
        border: 0;
        outline: 0;
        font-size: 100%;
        vertical-align: baseline;
    }
    #projects-wrapper .columns { margin-bottom: 36px; }
    .project-item .item-wrap {
    overflow: hidden;
    position: relative;

    -webkit-transition: all 0.3s ease-in-out;
        -moz-transition: all 0.3s ease-in-out;
        -o-transition: all 0.3s ease-in-out;
        -ms-transition: all 0.3s ease-in-out;
        transition: all 0.3s ease-in-out;
    }
    .project-item .item-wrap a {
    display: block;
    cursor: pointer;
    }

    /* overlay */
    .project-item .item-wrap .overlay {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

        opacity: 0;
        -moz-opacity: 0;
        filter:alpha(opacity=0);

    -webkit-transition: opacity 0.3s ease-in-out;
        -moz-transition: opacity 0.3s ease-in-out;
        -o-transition: opacity 0.3s ease-in-out;
        transition: opacity 0.3s ease-in-out;

    background: url(../../public/images/overlay-bg.png) repeat;
    }
    .project-item .item-wrap .link-icon {
    display: block;
    color: #fff;
    height: 30px;
    width: 30px;
    font-size: 18px;
    line-height: 30px;
    text-align: center;

    opacity: 0;
        -moz-opacity: 0;
        filter:alpha(opacity=0);

    -webkit-transition: opacity 0.3s ease-in-out;
        -moz-transition: opacity 0.3s ease-in-out;
        -o-transition: opacity 0.3s ease-in-out;
        transition: opacity 0.3s ease-in-out;

    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -15px;
    margin-top: -15px;
    }
    .project-item .item-wrap img {
    vertical-align: bottom;
    height: 270px;
    width: 100%;
    }
    .project-item .project-item-meta { padding: 18px }
    .project-item .project-item-meta h5 {
    font: 14px/21px 'opensans-bold', sans-serif;
    color: #fff;
    }
    .project-item .project-item-meta p {
    font: 12px/18px 'opensans-light', sans-serif;
    color: #c6c7c7;
    margin-bottom: 0;
    }

    /* on hover */
    .project-item:hover .overlay {
        opacity: 1;
        -moz-opacity: 1;
        filter:alpha(opacity=100);
    }
    .project-item:hover .link-icon {
    opacity: 1;
        -moz-opacity: 1;
        filter:alpha(opacity=100);
    }

    /* popup modal */
    .popup-modal {
        max-width: 550px;
        background: #fff;
        position: relative;
        margin: 0 auto;
    }
    .popup-modal .description-box { padding: 12px 36px 18px 36px; }
    .popup-modal .description-box h4 {
    font: 15px/24px 'opensans-bold', sans-serif;
        margin-bottom: 12px;
    color: #111;
    }
    .popup-modal .description-box p {
        font: 14px/24px 'opensans-regular', sans-serif;
    color: #A1A1A1;
    margin-bottom: 12px;
    }
    .popup-modal .description-box .categories {
    font: 11px/21px 'opensans-light', sans-serif;
    color: #A1A1A1;
    text-transform: uppercase;
    letter-spacing: 2px;
    display: block;
    text-align: left;
    }
    .popup-modal .description-box .categories i {
    margin-right: 8px;
    }
    .popup-modal .link-box {
    padding: 18px 36px;
    background: #111;
    text-align: left;
    }
    .popup-modal .link-box a {
    color: #fff;
        font: 11px/21px 'opensans-bold', sans-serif;
        text-transform: uppercase;
    letter-spacing: 3px;
    cursor: pointer;
    }
    .popup-modal a:hover {	color: #00CCCC; }
    .popup-modal a.popup-modal-dismiss { margin-left: 24px; }
}